import './Users.css'
import {useLocation } from 'react-router-dom'
const Users = () => {
    const location = useLocation()
    let users = location.state;
 
    return ( 
        
        <div className='users'>
            <h1>All Scores</h1>
            <ul>
                {
                    users!==undefined?users.map((each,i)=><li key={i}>{i+1}- {each.name} <p>{each.score}</p></li>):null                        
                }
            </ul>
        </div>
     );
}
 
export default Users;