import './Gameover.css'
const Gameover = (props) => {
    return ( 
        <div className='gameover' style={props.gameover?{display:'block'}:{display:'none'}}>
            <strong>Gameover </strong>
            <br /> <strong>Your Score: {props.score}</strong>
            <br />
            {props.newName==null?"Since you aren't signed in, your score hasn't been recorded":
            props.serverData?'Your score has been recorded':
            "Sorry your score hasn't been recorded. To record your score you need to use a VPN"}
            <br />
            <button onClick={props.tryAgainHandler}
            ><strong>Try again</strong></button>
          </div>
     );
}
 
export default Gameover;