import {Link} from 'react-router-dom'
import Loading from '../Loading/Loading';
import './Information.css'
const Information = (props) => {

    return ( <div className='info' style={props.appInfo?{display:'block'}:{display:'none'}}>
      <div  >
        {props.isPending?
        <Loading/>:
        (props.serverData&&props.sortUsers!==null)?
        <div >
        <p><strong>{props.numberOftimes}</strong> people are playing this game.</p>
          <p>Highest Scores:</p>
          <strong><ul className='scores'>
                      <li>{props.sortUsers[0]!==undefined?<p>{props.sortUsers[0].name}  <span>{props.sortUsers[0].score}</span></p>:null}</li>
                      <li>{props.sortUsers[1]!==undefined?<p>{props.sortUsers[1].name}  <span>{props.sortUsers[1].score}</span></p>:null}</li>
                      <li>{props.sortUsers[2]!==undefined?<p>{props.sortUsers[2].name}  <span>{props.sortUsers[2].score}</span></p>:null}</li>
                    </ul>
        <Link to={{pathname:'/Users',state:props.sortUsers,data:props.informationHandler}} >See all the scores here</Link>
          </strong>
        </div>:
        <p id='oops'><strong>Oops!</strong>
        <br />
        <br />
         Failed To Connect To The Server. We are using firebase service which is suspended for your country, Please use a VPN to sign in or see the Scores.
        </p>}
        <div className='guest'>
        {props.signedIn&&props.menu?
        <p>Hi <strong>{props.newName}</strong> !
        <br />
         Welcome to Simon game. </p>:
        null}
        </div>
      </div>
        
      </div> );
}

export default Information;